
import { useEmployeeTripStore } from '@/store/employee/trip'
import { computed, defineComponent } from 'vue'
import * as toastr from 'toastr';

export default defineComponent({
    setup() {
        const EmTrip = useEmployeeTripStore();
        const form = computed(() => EmTrip.getSelectedRequest)

        const updateRequest = async () => {
            await EmTrip.updateEmployeeTripRequest(form.value, form.value?.id)
            await EmTrip.setComponentTo('trip-request-component');
            toastr.success('Trip request updated.');            
        }

        return {
            EmTrip,
            form,
            updateRequest
        }
    },
})
